.background-cube {
  display: none;
  position: absolute;
  z-index: 10;
  width: 150vw;
  height: 150vw;
}

.background-cube.left {
  -webkit-transform: rotate(15deg);
          transform: rotate(15deg);
  -webkit-transform-origin: top left;
          transform-origin: top left;
  background: #c7a07a;
  top: 175%;
}

.background-cube.right {
  -webkit-transform: rotate(-15deg);
          transform: rotate(-15deg);
  -webkit-transform-origin: top right;
          transform-origin: top right;
  background: #c7a07a;
  top: 270%;
}

@media screen and (max-width: 575px) {
  .background-cube {
    display: none;
  }
}
#navbar {
    position: fixed;
    width: 100%;
    background-color: #F7BC6A;
    color: #210002;
    z-index: 1000;
}

#navbar .navbar-nav {
    margin-top: 0;
    vertical-align: middle;
}

#navbar .navbar-toggler {
    border: 0px;
}

#navbar .navbar-toggler:focus {
    outline: 0px;
}

.nav-item {
    margin: 0px 10px;
    position: relative;
    cursor: pointer;
}

#navbar .navbar-brand {
    cursor: pointer;
}

.nav-item a {
    vertical-align: middle;
    margin: 0;
}

.nav-item:hover {
    color: #a23d1b;
}

.nav-item a::before, .nav-item a::after {
    position: absolute;
    display: block;
    content: '';
    background-color: #a23d1b;
    height: 2px;
    width: 0;
    -webkit-transition: all 0.3s ease;
    transition: all 0.3s ease;
}

.nav-item a::before {
    left: 0;
    top: -2px;
}

.nav-item a::after {
    right: 0;
    bottom: -2px;
}

.nav-item a:hover::before, .nav-item a:hover::after {
    width: 100%;
}

@media (max-width: 575px) {
    .nav-item a::before, .nav-item a::after {
        display: none;
    }
}
