}

#footer ul#buttons li a {
	display: block;
	widows: 100%;
	height: 100%;
	font-size: 1.25em;
	background: transparent;
	-webkit-transition: .5s;
	transition: .5s;
	-webkit-animation: icon-out .5s forwards;
	        animation: icon-out .5s forwards;
	-webkit-animation-timing-function: cubic-bezier(0.5, -0.6, 1, 1);
	        animation-timing-function: cubic-bezier(0.5, -0.6, 1, 1);
}

#footer ul#buttons li:before {
	content: "";
	width: 90px;
	height: 90px;
	display: block;
	position: absolute;
	-webkit-transform: rotate(-45deg) translate(-110%, -23px);
	        transform: rotate(-45deg) translate(-110%, -23px);
	z-index: -2;
	-webkit-animation: back-out .5s forwards;
	        animation: back-out .5s forwards;
	-webkit-animation-timing-function: cubic-bezier(0.5, -0.6, 1, 1);
	        animation-timing-function: cubic-bezier(0.5, -0.6, 1, 1);
}

#footer ul#buttons li:hover a {
	-webkit-animation: icon-in .5s forwards;
	        animation: icon-in .5s forwards;
	-webkit-animation-timing-function: cubic-bezier(0, 0, 0.4, 1.6);
	        animation-timing-function: cubic-bezier(0, 0, 0.4, 1.6);
}

#footer ul#buttons li:hover:before { 
	-webkit-animation: back-in .5s forwards; 
	        animation: back-in .5s forwards;
	-webkit-animation-timing-function: cubic-bezier(0, 0, 0.4, 1.6);
	        animation-timing-function: cubic-bezier(0, 0, 0.4, 1.6);
}

@-webkit-keyframes back-in {
    0% { -webkit-transform: rotate(-45deg) translate(-110%, -23px); transform: rotate(-45deg) translate(-110%, -23px); }
	80% { -webkit-transform: rotate(-45deg) translate(5%, -23px); transform: rotate(-45deg) translate(5%, -23px); }
	100% { -webkit-transform: rotate(-45deg) translate(0%, -23px); transform: rotate(-45deg) translate(0%, -23px); }
}

@keyframes back-in {
    0% { -webkit-transform: rotate(-45deg) translate(-110%, -23px); transform: rotate(-45deg) translate(-110%, -23px); }
	80% { -webkit-transform: rotate(-45deg) translate(5%, -23px); transform: rotate(-45deg) translate(5%, -23px); }
	100% { -webkit-transform: rotate(-45deg) translate(0%, -23px); transform: rotate(-45deg) translate(0%, -23px); }
}

@-webkit-keyframes back-out {
    0% { -webkit-transform: rotate(-45deg) translate(0%, -23px); transform: rotate(-45deg) translate(0%, -23px); }
	20% { -webkit-transform: rotate(-45deg) translate(5%, -23px); transform: rotate(-45deg) translate(5%, -23px); }
	100% { -webkit-transform: rotate(-45deg) translate(-110%, -23px); transform: rotate(-45deg) translate(-110%, -23px); }
}

@keyframes back-out {
    0% { -webkit-transform: rotate(-45deg) translate(0%, -23px); transform: rotate(-45deg) translate(0%, -23px); }
	20% { -webkit-transform: rotate(-45deg) translate(5%, -23px); transform: rotate(-45deg) translate(5%, -23px); }
	100% { -webkit-transform: rotate(-45deg) translate(-110%, -23px); transform: rotate(-45deg) translate(-110%, -23px); }
}

@-webkit-keyframes icon-in {
    0% { font-size: 1.25em; }
	100% { color: #fff; font-size: 1.5em; }
}

@keyframes icon-in {
    0% { font-size: 1.25em; }
	100% { color: #fff; font-size: 1.5em; }
}

@-webkit-keyframes icon-out {
	0% { color: #fff; font-size: 1.5em; }
	100% { font-size: 1.25em; }
}

@keyframes icon-out {
	0% { color: #fff; font-size: 1.5em; }
	100% { font-size: 1.25em; }
}
